.badge {
  padding: 1px 15px;
  border-width: 1px;
  border-style: solid;
  @include typography(14px, 400);
  &.badge-outline-gray:not(.rounded-pill) {
    padding: 1px 7px;
  }
  &.badge-lg{
    padding: 5px 15px 4px;
  }
  &.round-fill{
    border-radius: 16px;
  }
}
@each $color, $value in $color-number {
  .badge-#{$color} {
    border-color: $value;
    background-color: $value;
  }
  .badge-outline-#{$color} {
    color: $value;
    border-color: $value;
    background-color: var(--white);
  }
}

.badge-gray {
  border-color: var(--gray3);
  background-color: var(--gray3);
}
.badge-outline-gray {
  color: var(--gray3);
  border-color: var(--gray5);
}
