.caution-box{
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: #FFF1ED;
  border-radius: 12px;
  padding: 12px 16px;
  margin-bottom: 1.5rem;
  p{
    color: var(--red500);
  }
}
.sortable-table{
  thead{
    th{ 
      background-color: #fff;
      border-right: 1px solid var(--gray5);
    }
  }
  tbody{
    td{
      border-right: 1px solid var(--gray5);
      &:last-child{
        border-right: 0;
      }
    }
    tr{ 
      cursor: pointer;
      &:last-child{
        td{
          border-bottom: 0;
          &:first-child{
            border-radius: 0 0 0 12px;
          }
          &:last-child{
            border-radius: 0 0 12px 0;
          }
        }
      }
    }
  }
}

.add-period-btn{
  width: 100%;  
  padding: 1rem;
  @include typography(14px, 600);
  color: var(--blue500);
  border: 1px dashed var(--blue500);
  border-radius: 4px;
  margin-top: 1.5rem;
}

.table-head-group{
  display: flex; 
  align-items: center; 
  justify-content: end;
  margin: 1.5rem 0;
}

.sale-price-table{
  tbody{
    tr{
      &:first-child{
        td{
          background-color: var(--gray7);
          @include typography(13px, 700);
          color: var(--gray2);
        }
      }
    }
    td{
      border-right: 1px solid var(--gray5);
    }
  }
  .date-box{
    &.semi-peak-season{
      .season{
        color: var(--green500);
      }
    }
    &.peak-season{
      .season{
        color: var(--red500);
      }
    }
    &.christmas{
      .season{
        color: var(--orange500);
      }
    }
    &.saturday{
      .week{color: var(--blue700);}
      .day{color: var(--blue700);}
    }
    &.sunday{
      .week{color: var(--red500);}
      .day{color: var(--red500);}
    }
    .week{
      color: var(--gray2);
      @include typography(13px, 700);
      @include media-breakpoint-down(lg){
        @include typography(12px, 700);
      }
    }
    .day{
      color: var(--gray1);
      @include typography(26px, 700);
      @include media-breakpoint-down(lg){
        @include typography(20px, 700);
      }
    }
    .season{
      @include typography(14px, 700);
      @include media-breakpoint-down(lg){
        @include typography(12px, 700);
      }
    }
  }
}
