.dashboard-body {
  background-color: var(--table-cell);
  @include media-breakpoint-down(lg){
    background-color: var(--blue500);
    #navbar{
      background-color: var(--blue500);
    }
    #main{
      background-color: var(--blue500);
    }
    #container{
      background-color: transparent;
      padding: 0;
    }
    .dashboard{
      height: 100%;
      .search-bar{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        padding: 0 20px;
        margin-bottom: 20px;
        .today-date{
          @include typography(18px, 700);
          color: #fff;
        }
        .btn-group{
          gap: 12px;
          .btn{border-radius: 8px; width: auto;}
        }
        .btn-search{
          background: rgba(255, 255, 255, 0.10);
          padding: 4px;
          i{width: 24px; height: 24px;}
        }
      }
      .today-list{
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 0 20px;
        li{
          width: 25%;
          background-color: rgba(255, 255, 255, 0.10);
          border-radius: 12px;
          display: flex;
          flex-direction: column;
          gap: 4px;
          text-align: center;
          padding: 10px 6px;
          .title{
            @include typography(13px, 500);
            color: #fff;
          }
          .cnt{
            @include typography(18px, 700);
            color: #fff;
          }
        }
      }
    }
    .list-table{
      background-color: #fff;
      min-height: 100%;
      padding: 24px 16px; 
      border-radius: 24px 24px 0 0;
      margin-top: 24px;
    }
  }
}

.dashboard {
  .card {
    height: 100%;
  }
  .table{
    // th,
    // td{white-space: nowrap;}
  }
}

.dashboard-display-vertical {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  gap: 0.5rem;
  li {
    flex-grow: 1;
  }
  .item {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.5rem;
    border: 1px solid $border-color;
    border-radius: 8px;
    padding: 0.5rem 1rem;
    background: var(--table-cell);
    transition: background-color 0.2s;
    .status {
      @include typography(16px);
      color: var(--gray2);
    }
    .num {
      @include typography(24px, 700);
    }
  }
  a.item {
    &:hover {
      text-decoration: none;
      background-color: var(--sub1);
    }
  }
}

.dashboard-cs {
  border-radius: 1rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 24px;
  &-header {
    flex: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .buttons {
      display: inline-flex;
      gap: 4px;
      flex-wrap: wrap;
    }
    .date {
      color: var(--gray4);
    }
  }
  &-body {
    flex-grow: 1;
  }
  .title {
    @include typography(18px, 700);
  }
}

.doughnut-chart-legend {
  dl {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    dt {
      @include typography(12px, 400);
      flex: 0 0 44px;
    }
    dd {
      margin: 0;
      flex: 1 1 calc(100% - 44px);
    }
    dt,
    dd {
      &:not(:first-of-type) {
        margin-top: 12px;
      }
    }
  }
}
.doughnut-chart {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  canvas {
    width: 160px !important;
    height: 160px !important;
  }
  &.flatform-list{
    canvas {
      width: 300px !important;
      height: 300px !important;
      @include media-breakpoint-down(lg){
        width: 240px !important;
        height: 240px !important;
      }
    }
  }
  .count {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: var(--gray2);
    strong {
      @include typography(32px, 700);
      color: var(--gray1);
    }
  }
}
.barChart{
  height: 190px !important;
}
.barChart-legend{
  text-align: center;
  @include typography(14px, 500);
  color: var(--gray2);
}
.dashboard-cs-items {
  display: grid;
  gap: 12px;
  grid-template-columns: repeat(2, 1fr);
  @include media-breakpoint-up(sm) {
    grid-template-columns: repeat(3, 1fr);
  }
  @include media-breakpoint-up(lg) {
    gap: 12px 24px;
  }
  .cs-item {
    border: 1px solid var(--gray5);
    border-radius: 8px;
    padding: 12px 16px;
    .title {
      @include typography(16px);
    }
    .count {
      @include typography(20px, 700);
    }
    a.count {
      &:hover {
        color: var(--main);
      }
    }
    .progress {
      --bs-progress-height: 1.5rem;
      margin-top: 8px;
    }
  }
}

.dashboard-stock {
  display: grid;
  gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  @include media-breakpoint-up(lg) {
    gap: 12px 24px;
  }
  .stock-item {
    border-radius: 8px;
    border: 1px solid var(--gray5);
    overflow: hidden;
    &-header {
      border-bottom: 1px solid var(--gray5);
      padding: 8px 12px;
    }
    &-body {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 120px;
      background: var(--table-cell);
      padding: 8px 12px;
    }
    .tag {
      @include typography(16px, 700);
    }
    .count {
      display: flex;
      align-items: flex-end;
      color: var(--gray3);
      text-align: center;
      strong {
        margin-bottom: 0.5rem;
        @include typography(32px, 700);
        @include media-breakpoint-down(xl) {
          @include typography(24px, 700);
        }
      }
    }
    &.prototype {
      border-style: dashed;
      &-header {
        border-style: dashed;
      }
      .count {
        strong {
          @include typography(20px, 700);
        }
      }
    }
  }
  a.stock-item {
    &:hover {
      border-color: var(--main);
      text-decoration: none;
    }
  }
}

.dashboard-stock-legend {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  margin-top: 1rem;
  .legend {
    color: var(--gray3);
    &::after {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 1px;
      height: 8px;
      background: var(--gray4);
      margin: 0 12px;
    }
  }
  dl {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px;
    margin: 0;
    dt {
      width: 8px;
      height: 8px;
      border-radius: 50%;
    }
    dd {
      color: var(--gray3);
      margin: 0;
      & + dt {
        margin-left: 8px;
      }
    }
  }
}

.search-bar{
  display: flex; align-items: center; justify-content: space-between;
  padding-top: 1.5rem;
}
.card{
  &.today-current-situation{
    display: flex; 
    flex-direction: row;
    background-color: var(--gray7);
    @include media-breakpoint-up(xl){
      &.lg{
        padding: 56px 40px;
        height: 100%;
      }
      padding: 32px 40px;
      align-items: center;
      justify-content: space-between;
    }
    .date{
      @include typography(26px, 700); 
    }
    .today-list{
      display: flex;
      align-items: center;
      gap: 40px;
      @include media-breakpoint-down(lg){
        width: 100%;
       gap: 20px; 
       flex-flow: wrap;
      }
      @include media-breakpoint-up(xl){
        gap: 64px;
      }
      li{
        display: flex;
        align-items: center;
        gap: 12px;
        @include media-breakpoint-down(lg){
          width: calc(50% - 10px);
          justify-content: center;
          padding: 12px 0;
        }
        .title{
          @include typography(15px, 500); 
          color: var(--gray2);
        }
        .cnt{
          @include typography(26px, 700);
        }
      }
     
    }
  }
}
.user-info{
  .name{
    @include typography(14px, 700); 
    color: var(--gray1);
  }
  .phone-number{
    @include typography(14px, 500); 
    color: var(--gray2);
  }
}

.notice-wrap{
  padding: 0 1.5rem 1.5rem;
  max-height: 230px;
  overflow: auto;
  li{
    padding: 0.75rem 0;
    border-bottom: 1px solid var(--gray5);
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    .event-badge{
      @include typography(14px, 500);
      color: var(--orange500);
    }
    .notice-badge{
      @include typography(14px, 500); 
      color: var(--blue500);
    }
    .desc{
      @include typography(14px, 500); 
      width: 90%;
      display: inline-block;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}

.modal-sale-price{
  .selected{
    background-color: var(--gray6);
  }
  .price{
    display: flex; align-items: center; gap: 4px;
  }
  .sell-channel{
    display: flex; 
    .form-check{
      width: 50%;
    }
  }
}