.detail-table{
  border-radius: 12px;
  padding: 24px 32px;
  border: 1px solid var(--gray5);
  @include media-breakpoint-down(lg){
    padding: 8px 16px;
  }
  &.td-border{
    tbody{
      td{
        border-right: 1px solid var(--gray5);
        &:last-child{
          border-right: 0;
        }
      }
    }
  }
  .table{
    border: 0;
  }
  thead{
     th{
      border-bottom: 1px solid var(--gray5);
    }
  }
  tbody{
    tr{
      border-bottom: 1px solid var(--gray5);
      &:first-child{
        border-top: 0;
      }
      &:last-child{
        border-bottom: 0;
      }
    }
    th{
      @include media-breakpoint-down(lg){
        padding: 1rem 0.75rem 0.25rem;
        font-size: 14px;
        text-align: left !important;
      }
    }
    th,td{
      background-color: #fff;
      border: 0;
      @include media-breakpoint-down(lg){
        display: block;
      }
    }
  }
  .detail-title{
    width: 90px; font-size: 13px; color: var(--gray2); font-weight: 700; flex-shrink: 0;
    @include media-breakpoint-down(lg){
      width: 75px;
    }
  }
  .error-txt{
    margin-top: 16px;
  }
}
.price-info-table{
  border-radius: 12px;
  padding: 24px 32px;
  border: 1px solid var(--gray5);
  @include media-breakpoint-down(lg){
    padding: 8px 16px;
  }
  .table{border: 0;}
  thead{
    th{
     border-bottom: 1px solid var(--gray5);
   }
 }
  tbody{
    tr{
      border-bottom: 1px solid var(--gray5);
    }
    td{
      border-right: 1px solid var(--gray5);
      &:last-child{
        border-right: 0;
      }
    }
  }
}
.item-list{
  display: flex;
  align-items: center;
  gap: 16px;
  @include media-breakpoint-down(lg){
    flex-direction: column;
    align-items: end;
  }
  .check-wrap{
    width: 100%; display: none; align-items: center; flex-wrap: wrap; padding-left: 12px; gap: 16px;
    @include media-breakpoint-down(md){
      padding-left: 0;
    }
    .form-check{
      width: calc(20% - 14px);
      @include media-breakpoint-down(md){
        width: calc(50% - 8px);
      }
    }
  }
  .input-wrap{
    width: 100%; display: flex; align-items: center; flex-wrap: wrap; padding-left: 12px; gap: 16px;
    @include media-breakpoint-down(md){
      padding-left: 0;
    }
    .form-control{
      width: calc(20% - 14px);
      @include media-breakpoint-down(md){
        width: calc(50% - 8px);
      }
    }
    .add-item{
      border: 1px dashed var(--blue500);
      width: calc(20% - 14px);
      @include typography(14px, 600);
      color: var(--blue500);
      background-color: #fff;
      padding: 4px 10px 5px;
      border-radius: 4px;
      @include media-breakpoint-down(lg){
        width: calc(50% - 8px);
      }
    }
  }
  .item-save-btn{
    background-color: var(--gray1);
    color: #fff;
    flex-shrink: 0;
  }
  &.active{
    .check-wrap{display: flex;}
    .input-wrap{display: none;}
    .item-save-btn{
      background-color: var(--gray3);
    }
  }
}
.select-wrap{
  display: flex;
  align-items: center;
  flex-flow: wrap;
  gap: 1.5rem;
  .form-select{
    width: 165px;
  }
  @include media-breakpoint-down(lg){
    gap: 1rem;
    .form-select{
      width: auto;
    }
  }
}

.notice-box{
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: #F1F7FC;
  border-radius: 12px;
  padding: 12px 16px;
  margin-bottom: 1.5rem;
  p{
    color: var(--blue500);
  }
}

.img-file-wrap{
  display: flex; flex-wrap: wrap; gap: 16px;
}
.file-upload-container{
  position: relative;
  display: inline-block;
  overflow: hidden;
  width: calc(25% - 12px);
  @include media-breakpoint-down(lg){
    width: calc(50% - 12px);
  }
  @include media-breakpoint-down(md){
    width: 100%;
  }
}
.file-content {
  width: 100%;
  position: relative;
  @include media-breakpoint-down(lg){
    width: 100%;
  }
  .representative-img{
    position: absolute;
    top: 8px;
    left: 8px;
    background-color: var(--blue500);
    color: var(--white);
    @include typography(13px, 500);
    padding: 2px 12px;
    border-radius: 2px;
    z-index: 100;
  }
  .img-close-btn{display: none; position: absolute; top: 8px; right: 8px; z-index: 15;}
  &.close{
    .img-close-btn{
      display: block;
    }
  }
}
.file-upload-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.file-upload-button {
    padding: 8px 16px;
    background-color: var(--gray7);
    border-radius: 8px;
    cursor: pointer;
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 8px;
    color: var(--blue500);
    border: 1px dashed var(--blue500);
}
.file-preview {
  width: 100%;
  height: 210px;
  @include media-breakpoint-down(lg){
    height: 160px;
  }
  img{
    width: 100%; height: 100%; object-fit: cover;
    position: relative;
    z-index: 10;
    border-radius: 8px;
  }
 
}
.img-name{
  margin: 10px 0;
}

.order-btn-group{
  margin-top: 24px;
  .drag-btn{
    display: none;
  }
  .order-btn{
    display: block;
  }
  &.active{
    .drag-btn{
      display: flex;
    }
    .order-btn{
      display: none;
    }
  }
}

.message-wrap{
  border: 1px solid var(--gray5);
  border-radius: 12px;
  padding: 1.5rem;
  .title{
    padding-bottom: 1.5rem;
    border-bottom: 1px solid var(--gray5);
    @include typography(18px, 700);
    text-align: center;
  }
  .message-form{
    display: flex; 
    flex-direction: column; 
    gap: 8px; 
    padding: 1.5rem 0;
    .form-control{ background-color: #fff;}
  }
  .text-input{
    border: 1px solid var(--gray5);
    padding: 14px 12px;
    position: relative;
    border-radius: 8px;
    .form-textarea{
      border:0;
      resize: none;
      min-height: 240px;
    }
    .count{
      position: absolute;bottom: 14px;right: 12px;
      color: var(--gray3);
      @include typography(13px, 400);
    }
  }
}

.confirmation-wrap{
  .main-title{
    @include typography(32px, 700); text-align: center; margin-bottom: 1.5rem;
  }
  .table-title{
    padding: 1rem 0.75rem;
    border-bottom: 1px solid var(--gray5);
  }
}

.accommodation-info-wrap{
  padding: 1.5rem 1rem; 
  background-color: var(--gray7);
  border: 1px solid var(--gray5);
  display: flex;
  flex-direction: column;
  gap: 8px;
  border-radius: 12px;
  .title{
    @include typography(16px, 700);
  }
  .sign-file{
    width: 100%; 
    flex: 0 0 20%;
    display: flex; 
    align-items: center; 
    gap: 16px;
    .file-upload-container{width: 100%;}
    p{ flex-shrink: 0;}
    .file-upload-button{
      border: 1px dashed var(--gray5);
      background-color: #fff;
      color: var(--gray3);
    }
    .file-preview{
      height: 60px;
    }
  }
}

.setting-inp{
  width: 225px;
  @include media-breakpoint-down(lg){
    width: 100%;
  }
}
.setting-select{
  width: 165px;
  @include media-breakpoint-down(lg){
    width: auto;
  }
}
