.comment-wrap {
  .comment {
    &:not(:first-child) {
      border-top: 1px solid #e7e7e7;
      margin-top: 24px;
      padding-top: 24px;
    }
    .name {
      font-weight: 700;
    }
    .desc {
      color: #4f4f4f;
      padding: 12px 0;
    }
    .comment-date {
      padding: 12px 0;
      display: flex;
      align-items: center;
      gap: 8px;
      span {
        color: #828282;
      }
      .btn {
        font-weight: 700;
        background-color: #fff;
      }
    }

    .reply-wrap {
      padding: 12px;
      background-color: #f5f5f5;
      color: #4f4f4f;
    }
  }
  textarea {
    border-radius: 12px;
    background-color: #f5f5f5;
  }
}
