.btn{
  flex-shrink: 0;
  white-space: nowrap;
  display: inline-flex;align-items: center;justify-content: center;gap: 8px;
  --bs-btn-font-weight: 700;
  padding: 11px 24px 10px;
  line-height: 150%;
  @include media-breakpoint-down(lg){
    padding: 8px 16px;
  }
  &:hover{
    text-decoration: none;
  }
  &:disabled{
    opacity: 0.3;
  }
  .input-group > &{
    min-width: 0;
  }
  &.px-3{
    padding-left: 10px !important;padding-right: 10px !important;
  }
  .table &{
    padding-left: 11px;padding-right: 11px;
  }
}

.btn-sm{
  @include typography(14px);
  padding: 6px 24px 5px;
}
.btn-lg{
  @include typography(16px);
  padding: 12px 24px;
}
.btn-gray{
  background-color: var(--gray6);
  border: 1px solid var(--gray6);
  color: var(--gray2);
  &:hover{
    color: var(--gray3);
    border-color: shade-color(#e7e7e7, $btn-hover-bg-shade-amount);
    background-color: shade-color(#e7e7e7, $btn-hover-bg-shade-amount);
  }
}

.btn-gray6{
  background-color: var(--gray6);
  color: var(--gray2);
}
.btn-dark{
  &:hover{
    border-color: shade-color(#4f4f4f, $btn-hover-bg-shade-amount);
    background-color: shade-color(#4f4f4f, $btn-hover-bg-shade-amount);
  }
}

.btn-outline-gray{
  --bs-btn-hover-color: var(--gray3) !important;
  --bs-btn-hover-bg: var(--bs-btn-bg) !important;
  --bs-btn-hover-border-color: var(--gray4) !important;
  border-color: var(--gray4);
  color: var(--gray3);
}

[class*=btn-outline-]{
  --bs-btn-hover-color: var(--bs-btn-color);
  --bs-btn-hover-bg: var(--bs-btn-bg);
  --bs-btn-hover-border-color: var(--bs-btn-border-color);
  &:hover{
    opacity: .8;
  }
}
.close-btn{
  border: 1px solid var(--gray3);
  border-radius: 8px;
  padding: 4px;
  
}


