.treemenu{
}
.treemenu-header{
  margin-bottom: .5rem;
}
.treemenu-body{
  background: var(--gray6);
  border: 1px solid var(--gray5);
  border-bottom: 0;
  border-radius: 8px 8px 0 0;
  overflow: auto;
  max-height: 300px;
  @include media-breakpoint-up(lg) {
    height: 620px;
    max-height: 620px;
  }
}
.treemenu-foot{
  background: var(--gray6);
  padding: 1rem;
  border: 1px solid var(--gray5);
  border-radius: 0 0 8px 8px;
}

.tui-ico-file,
.tui-ico-folder,
.tui-ico-root{
  overflow: hidden;
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  background-repeat: no-repeat;background-position: 0 0 !important;
}
