.tui-ico-tree{

}

.tui-tree-wrap{
  width: 100%;
  background: transparent;
  padding: 1rem 0;
  .tui-tree-toggle-btn{
    margin-top: 7px;
    width: 16px;height: 16px;border-radius: 4px;
    &:hover{
      background-color: #f8f8f8;
    }
  }
}
.tui-tree-content-wrapper{
  width: auto;
}

.tui-tree-text{
  @include typography(14px)
}

.tui-tree-selected{
  > .tui-tree-content-wrapper{
     .tui-tree-text{color: $secondary;}
  }
}

.tui-tree-toggle-btn{

}