.modal{
  --bs-modal-border-radius: 0.625rem;
  --bs-modal-header-padding: 1.5rem;
  --bs-modal-padding: 1.5rem;
  @include media-breakpoint-down(lg){
    --bs-modal-header-padding: 1.25rem;
    --bs-modal-padding: 1.25rem;
  }
}
.btn-close{
  opacity: 1;
}
.modal-header{
  border-bottom: 0;
}

.modal-body{
}

.modal-title{
  @include typography(16px, 700);
  @include media-breakpoint-down(lg){
    @include typography(16px);
  }
}
.modal-form{
  display: flex;
  flex-direction: column;
  gap: 40px;
  @include media-breakpoint-up(lg){
    gap: 56px;
  } 
  &.gap-md{
    gap: 30px;
    @include media-breakpoint-up(lg){
      gap: 40px;
    } 
  }
  .logo{
    text-align: center;
  }
  .desc{
    text-align: center;
    @include typography(14px);
    p + p{
      margin-top: 1rem;
    }
    @include media-breakpoint-up(lg){
      @include typography(16px);
    } 
  }
  .call{
    text-align: center;
    @include media-breakpoint-up(lg){
      @include typography(16px, 700);
    } 
  }
}
.modal-desc{
  margin-bottom: 1.5rem;
}

.modal-footer{
  border-top: 0;
  width: 100%;
  padding: 1.5rem;
  @include media-breakpoint-down(lg){
    padding: 1.25rem;
  }
  > *{margin: 0;}
  .d-flex{
    width: 100%;gap: .5rem;
    > .btn{
      flex: 1;
    }
  }
  &.find{
    padding: 1.5rem 1.5rem 9.125rem;
  }
}
.sign-up{
  .modal-footer{
    padding: 1.5rem;
  }
}
.modal-mypage{
  @include media-breakpoint-down(lg){
    margin: 0;
    height: 100%;
    .modal-content{
      height: 100%;
      border-radius: 0;
      border: 0;
      .table{
        border: 1px solid transparent;
      }
      tbody{
        tr{
          border-bottom: 1px solid var(--gray6);
        }
        th{
          text-align: left !important;
          display: block;
          background-color: #fff;
          padding: 1rem 0.75rem 0 0.75rem;
        }
        td{
          display: block;
        }
      }
    }
    .user-info{
      display: flex; align-items: center; gap: 8px;
      .user-id{
        @include typography(16px, 700);
        color: var(--gray1);
      }
    }
  } 
  .btns{
    margin-top: 12px;
    @include media-breakpoint-down(lg){
      margin-top: 80px;
    }
  }
}