.overflow-y-scroll {
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 100%;
}

.border-radius-4 {
  border-radius: 4px !important;
}
.border-radius-8 {
  border-radius: 8px !important;
}

.cursor-pointer {
  cursor: pointer;
}

.max-width-none {
  max-width: none !important;
}

.divider-dashed {
  border: 1px dashed $border-color;
  margin: 1rem 0;
}

.border-top-dark {
  border-top: 1px solid $dark !important;
}

.text-gray-3 {
  color: var(--gray3);
}
.font-bold{
  font-weight: bold !important;
}
.text-purple {
  color: var(--sub3);
}
.bg-purple {
  background-color: var(--sub3);
}
.min-width-750px {
  min-width: 750px;
}
.bg-light-green{
  background-color: var(--sub1) !important;
}
.font-sm{
  @include typography(12px, 400);
  color: var(--gray2);
}
.font-md{
  @include typography(16px, 500);
  color: var(--gray1);
}
.br-0{ border-right: 0 !important;}
.bt-0{ border-top: 0 !important;}
.bl-0{ border-left: 0 !important;}
.bb-0{ border-bottom: 0 !important;}
.br-1{ border-right: 1px solid var(--gray5) !important;}
.bt-1{ border-top: 1px solid var(--gray5) !important;}
.bl-1{ border-left: 1px solid var(--gray5) !important;}
.bb-1{ border-bottom: 1px solid var(--gray5) !important;}
.error-txt{
  @include typography(14px, 400);
  color: var(--red500);
}