.login{
  display: flex;align-items: center;justify-content: center;height: 100%;width: 100%;padding: 20px; background-color: $gray7;
}

.login-box{
  width: 580px;background: var(--gray7);padding: 40px 20px;
  display: flex;flex-direction: column;gap: 40px;
  border-radius: 1.5rem;
  text-align: center;
  @include media-breakpoint-up(lg){
    padding: 80px 120px;
    gap: 56px;
    background: #fff;
    text-align: start;
  }
  @include media-breakpoint-down(lg){
     
  }
}

.login-heading{
  display: flex;flex-direction: column;gap: 1rem;
  .img{
    @include media-breakpoint-up(lg){
      margin-bottom: 1rem;
    }
  }

  h1{
    @include typography(32px, 700);
    @include media-breakpoint-up(lg){
     
    }
  }
  .brand{
    @include typography(14px);
    @include media-breakpoint-up(lg){
      @include typography(16px);
    }
  }
}

.login-form{
  display: flex;flex-direction: column;gap: 40px;
  @include media-breakpoint-up(lg){
    gap: 56px;
  }
  .form{
    display: flex;flex-direction: column;gap: 12px;
  }
  .password-reset{
    color: var(--gray3);
    @include typography(14px, 600);
    margin-top: 1rem;
    @include media-breakpoint-up(lg){
    }
  }
}