$red: #E51414;
$blue: #26A4FF;
$brand: #0076F3;
$green: #43db80;
$pink: #FF7878;
$yellow: #f89500;
$dark: #262D34;
$gray: #e8e7e7;
$white: #fff;

$blue700: #05468A;
$blue500: #0076F3;
$blue200: #E2ECF8;
$blue50: #F1F7FC;
$yellow500: #FDC022;
$yellow200: #FFE5A3;
$yellow50: #FFF9EA;
$green700: #117D10;
$green500: #38AF21;
$green200: #C6F7A6;
$green50: #F2FFE8;
$red700: #A40A24;
$red500: #E51414;
$red200: #FCB8A0;
$red50: #FFF1ED;
$orange700: #B54C01;
$orange500: #FC8302;
$orange200: #FEDC99;
$orange50: #FFF5EA;
$gray1: #262D34;
$gray2: #5D6772;
$gray3: #A1A7AE;
$gray4: #CDD1D6;
$gray5: #E0E4EA;
$gray6: #EDF0F3;
$gray7: #F9FAFC;

$primary:       $brand
;
$secondary:     $pink;
$info:       $blue;
$success:       $brand;
$warning:       $yellow;
$danger:        $red;

$color-number: (
  "brand": $blue500,
  "danger": $danger,
  "blue700": $blue700,
  "blue500": $blue500,
  "blue200": $blue200,
  "blue50": $blue50,
  "yellow500": $yellow500,
  "yellow200": $yellow200,
  "yellow50": $yellow50,
  "green700": $green700,
  "green500": $green500,
  "green200": $green200,
  "green50": $green50,
  "red700": $red700,
  "red500": $red500,
  "red200": $red200,
  "red50": $red50,
  "orange700": $orange700,
  "orange500": $orange500,
  "orange200": $orange200,
  "orange50": $orange50,
  "gray1": $gray1,
  "gray2": $gray2,
  "gray3": $gray3,
  "gray4": $gray4,
  "gray5": $gray5,
  "gray6": $gray6,
  "gray7": $gray7,
);

$theme-colors: (
  "primary":    $primary,
  "secondary":  $secondary,
  "orange":     $orange500,
  "gray":    $success,
  "warning":    $warning,
  "danger":     $danger,
  "dark":       $dark,
  "yellow500": $yellow500,
  "yellow200": $yellow200,
  "yellow50": $yellow50,
  "green700": $green700,
  "green500": $green500,
  "green200": $green200,
  "green50": $green50,
  "red700": $red700,
  "red500": $red500,
  "red200": $red200,
  "red50": $red50,
  "orange700": $orange700,
  "orange500": $orange500,
  "orange200": $orange200,
  "orange50": $orange50,
  "gray1": $gray1,
  "gray2": $gray2,
  "gray3": $gray3,
  "gray4": $gray4,
  "gray5": $gray5,
  "gray6": $gray6,
  "gray7": $gray7,
);

$spacer: 1rem;
$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .5,
  3: $spacer * .75,
  4: $spacer,
  5: $spacer * 1.25,
  6: $spacer * 1.5,
  7: $spacer * 2,
  8: $spacer * 2.5,
  9: $spacer * 3.5,
);

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
  xxxl: 1600px
);

$grid-gutter-width: .5rem;

$min-contrast-ratio: 1.6;

$color-contrast-dark: $dark;
$color-contrast-light: $white;

$enable-rounded: true;
$enable-transitions: true;
$enable-validation-icons: false;
$enable-negative-margins: true;

$font-family-base: 'Pretendard', 'NanumGothic', system-ui, -apple-system, arial;

$font-size-base: .875rem;
$line-height-base: 1.4;

$body-color: #282828;
$link-color: $body-color;

$border-color: var(--gray5);
$border-color-translucent: $border-color;
$border-radius: .25rem;

$paragraph-margin-bottom: 0;

$component-active-bg: $primary;

$input-placeholder-color: var(--gray3);

$nav-link-padding-y: .625rem;

$input-btn-border-radius: .25rem;
$input-btn-padding-y: .7063rem;
$input-btn-padding-x: .75rem;
$input-btn-padding-y-lg: .625rem;
$input-btn-font-size-lg: 1rem;

$input-padding-y-lg: .8125rem;
$input-border-radius: .5rem;
$input-focus-box-shadow: 0 0 0 0.125rem rgba($component-active-bg, 0.25);
$input-disabled-color: var(--gray4);
$input-disabled-bg: var(--gray6);

$btn-padding-x: 1.4375rem;
$btn-padding-y: .5625rem;
$btn-padding-x-lg: 1.9375rem;
$btn-padding-y-lg: .9688rem;
$btn-padding-x-sm: .9375rem;
$btn-padding-y-sm: .1875rem;
$btn-font-weight: 700;
$btn-border-radius: .5rem;
$btn-border-radius-lg: .5rem;
$btn-border-radius-sm: .25rem;

$btn-hover-bg-shade-amount:       12.5%;
$btn-hover-bg-tint-amount:        12.5%;
$btn-hover-border-shade-amount:   15%;
$btn-hover-border-tint-amount:    7.5%;
$btn-active-bg-shade-amount:      10%;
$btn-active-bg-tint-amount:       10%;
$btn-active-border-shade-amount:  20%;
$btn-active-border-tint-amount: 7.5%;

$form-feedback-font-size: .875rem;

$focus-ring-width: .125rem;

$form-check-min-height: 1.125rem;

$form-text-color: var(--gray3);
$form-text-font-size: .875rem;

$card-border-radius: .5rem;

$modal-inner-padding: 1.25rem;
$modal-sm: 400px;
$modal-lg: 700px;
$modal-xl: 840px;

$pagination-active-bg: var(--gray2);
$pagination-active-border-color: var(--gray2);

$table-cell-padding-y: .75rem;
$table-cell-padding-x: .75rem;

$progress-height: 1rem;
$progress-border-radius: 10rem;
