.filter{
  border-radius: 8px;
  margin: 1.5rem 0;
  //overflow-y: hidden;
  .filter-title{
    @include typography(24px, 700);
    @include media-breakpoint-down(md){
      @include typography(18px, 700);
    }
  }
  &-item{
    display: flex;align-items: center;gap: .5rem;
  }
  .label{
    font-weight: 700;flex: 0 0 auto;
  }
  @include media-breakpoint-down(md){
    .hstack.gap-2{
      > .btn{
        flex: 1;
      }
    }
  }
}