.page-top{
  padding: 1rem 0;
  @include media-breakpoint-up(lg){
    display: flex;align-items: center;
    padding: 3rem 0 1.5rem;
    nav{
      margin-left: auto;
    }
  }
  .page-title{
    @include typography(16px, 700);
    @include media-breakpoint-up(lg){
      @include typography(32px, 700);
      margin-bottom: 0;
    }
  }
}
