.sales-management{
  display: flex; align-items: center; justify-content: space-between; gap: 40px;
  @media screen and (max-width: 1600px) {
    flex-direction: column;
  }
}
.flatform-list{
  width: 300px !important;
  height: 300px !important;
  @include media-breakpoint-down(lg){
    width: 240px !important;
    height: 240px !important;
  }
  #doughnut-chart{
    width: 100% !important;
    height: 100% !important;
  }
  .count{
    flex-direction: column;
    .title{
      @include typography(15px, 500);
      color: var(--gray2);
    }
    .price{
      @include typography(24px, 700);
      color: var(--gray1);
    }
  }
}

.statistics-period{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  @include media-breakpoint-down(lg){
    flex-direction: column;
    gap: 16px;
  }

}
.legend-list{
  display: flex;
  flex-direction: column;
  gap: 16px;
  &.horizontality{
    flex-direction: row;
  }
  .legent-content{
    display: flex; gap: 8px;
  }
  .legend-bg{
    width: 16px;
    height: 16px;
    border-radius: 4px;
    margin-top: 2px;
    &.channel{
      background-color: var(--blue500);
    }
    &.credit-card{
      background-color: var(--yellow500);
    }
    &.cash{
      background-color: var(--gray4);
    }
    &.flatform-1{
      background-color: var(--green500);
    }
    &.flatform-2{
      background-color: #FF3478;
    }
    &.flatform-3{
      background-color: #E83426;
    }
  }
  .legend-desc{
    .object{
      @include typography(14px, 500);
      color: var(--gray2);
    }
    .price{
      @include typography(15px, 700);
      color: var(--gray1);
    }
  }
}

.chart-wrap{
  .bar{
    canvas{
      height: 320px !important;
    }
  }
  @include media-breakpoint-down(lg){
  }
}
#myBarChart{
  width: 100% !important;
  height: 320px !important;
}

.tab-date-wrap{
  display: flex; 
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--gray6);
  @include media-breakpoint-down(lg){
    flex-direction: column;
    align-items: start;
    [data-picker=date]{
      width: 100%;
    }
  }
  .nav-tabs{
    border-bottom: 0;
    @include media-breakpoint-down(lg){
      margin-bottom: 1rem;
     
    }
  }
}