[class^="icon-"] {
  display: inline-block;
  vertical-align: middle;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  flex-shrink: 0 !important;
}

.material-symbols-outlined {
  vertical-align: middle;
  flex-shrink: 0;
}

/* gnb 아이콘 */
$gnb-icons: reservation, fee, payment, platform, setting, statistics;
@each $icon in $gnb-icons {
  .icon-gnb-#{$icon} {
    background-image: url("../images/icon-gnb-#{$icon}.svg");
    width: 24px;
    height: 24px;
  }    
}

