/* 16px 아이콘 */
$icons-16px: input-mail, input-key, check, inf, reply;
@each $icon in $icons-16px {
  .icon-#{$icon} {
    background-image: url("../images/icon-#{$icon}.svg");
    width: 16px;
    height: 16px;
  }
}

/* 20px 아이콘 */
$icons-20px: input-close, error, search-white, reset, edit, minus, plus, search, image, arrow-up, arrow-down, trash;
@each $icon in $icons-20px {
  .icon-#{$icon} {
    background-image: url("../images/icon-#{$icon}.svg");
    width: 20px;
    height: 20px;
  }
}

/* 22px 아이콘 */
$icons-22px: delete, close, list-check, btn-calendar, print, mail;
@each $icon in $icons-22px {
  .icon-#{$icon} {
    background-image: url("../images/icon-#{$icon}.svg");
    width: 24px;
    height: 24px;
  }
}

/* 24px 아이콘 */
$icons-24px: delete, close, g-back;
@each $icon in $icons-24px {
  .icon-#{$icon} {
    background-image: url("../images/icon-#{$icon}.svg");
    width: 24px;
    height: 24px;
  }
}

/* 32px 아이콘 */
$icons-32px: folder-open, kakao;
@each $icon in $icons-32px {
  .icon-#{$icon} {
    background-image: url("../images/icon-#{$icon}.svg");
    width: 32px;
    height: 32px;
  }
}
