:root{
  --sat: env(safe-area-inset-top);
  --sar: env(safe-area-inset-right);
  --sab: env(safe-area-inset-bottom);
  --sal: env(safe-area-inset-left);

  --white: #fff;

  --blue700: #05468A;
  --blue500: #0076F3;
  --blue200: #E2ECF8;
  --blue50: #F1F7FC;

  --yellow500: #FDC022;
  --yellow200: #FFE5A3;
  --yellow50: #FFF9EA;

  --green700: #117D10;
  --green500: #38AF21;
  --green200: #C6F7A6;
  --green50: #F2FFE8;

  --red700: #A40A24;
  --red500: #E51414;
  --red200: #FCB8A0;
  --red50: #FFF1ED;

  --orange700: #B54C01;
  --orange500: #FC8302;
  --orange200: #FEDC99;
  --orange50: #FFF5EA;

  --gray1: #262D34;
  --gray2: #5D6772;
  --gray3: #A1A7AE;
  --gray4: #CDD1D6;
  --gray5: #E0E4EA;
  --gray6: #EDF0F3;
  --gray7: #F9FAFC;

  --main: #0076F3; 

  --sub1: #F4F8E9;
  --sub2: #474B3D;
  --sub3: #8C39B3;

  --table-cell: #FBFBFB;

  --sidebar-bg: var(--sub2);
}

$navbar-height: 64px;
$navbar-height-lg: 85px;
$navbar-z-index: 99;

$sidebar-width: 280px;
$sidebar-slim-width: 72px;
$sidebar-z-index: 99;

$container-padding-x: 1rem;
$container-padding-x-lg: 2.5rem;